   font-style: normal;
}

body {
   font-family: "AvenirNext";
}

.test {
   z-index: 1;
   display: flex;
   justify-content: flex-end;
   transform: translateX(-5px);
}

.test::after {
   content: '';


}




.PhoneInput {
   /* display: flex; */
   gap: 10px;
   display: none;
}

.PhoneInputCountrySelect {
   position: absolute;
   top: 0;
   left: 0;
   z-index: 2;
   width: 100%;
   height: 100%;
   opacity: 0;
   cursor: pointer;
}

.PhoneInputCountry {
   position: relative;
   /*  width: 30px; */
   height: 20px;

}

.PhoneInputCountryIcon {

   width: 30px;
}

.PhoneInputInput {
   color: white;
   font-size: inherit;
}